<template>
  <div id="main"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(content) {
      console.log(content);
      let data1_1 = [];
      let data1_2 = [];
      let data1_3 = [];
      content.forEach((item) => {
        data1_1.push(item.DateStr1);
        data1_2.push(item.Elec1);
        data1_3.push(item.ElecEqmName1)
      });
      console.log(data1_3)

      let chart_projectLoad = this.$echarts.init(
        document.getElementById("main")
      );
      chart_projectLoad.clear();
      var option = {
        dataZoom: [
          {},
          {
            type: "inside",
          },
        ],
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: data1_3,
          // selected: data1_4,
          type: "scroll",
          orient: "vertical",
          left: "0%",
          textStyle: {
            color: "#fff",
          },
        },
        grid: {
          left: "25%",
          right: "1%",
          bottom: "2%",
          top: "3%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {
              show: false,
            },
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: true,
          data: data1_1,
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            name:data1_3[0],
            data: data1_2,
            type: "line",
          },
        ],
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  width: 100%;
  height: 100%;
}
</style>