<template>
  <div class="energyTrendAnalysis">
    <div class="right border-box">
      <div class="search chartSearch">
        <div class="tabAll" style="position: absolute; top: 0">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <span>园区</span>
          <el-select v-model="areaVal" placeholder="" @change="companyListFun">
            <el-option
              v-for="item in areaOptions"
              :key="item.areaId"
              :label="item.areaName"
              :value="item.areaId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>企业</span>
          <el-select
            v-model="companyVal"
            placeholder=""
            @change="companyelecstationFun"
          >
            <el-option
              v-for="item in companyOptions"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>电站</span>
          <el-select
            v-model="elecstationVal"
            placeholder=""
            @change="elecEqmListFun"
          >
            <el-option
              v-for="item in elecstationOptions"
              :key="item.elecStationId"
              :label="item.elecStationName"
              :value="item.elecStationId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>设备名称</span>
          <el-select v-model="elecEqmVal" placeholder="">
            <el-option
              v-for="item in elecEqmOptions"
              :key="item.elecEqmId"
              :label="item.elecEqmName"
              :value="item.elecEqmId"
            >
            </el-option>
          </el-select>
        </div>
        <button class="searchBth" @click="fdchartsFun()">查询</button>
      </div>
      <div style="width: 100%; display: flex">
        <div class="tabel">
          <el-table
            :data="tableData"
            style="width: 100%"
            height="250"
            @row-click="rowClick"
          >
            <el-table-column prop="DateStr1" label="日期"> </el-table-column>
            <el-table-column prop="AreaName1" label="园区名称" min-width="150">
            </el-table-column>
            <el-table-column
              prop="CompanyName1"
              label="企业名称"
              min-width="150"
            >
            </el-table-column>
            <el-table-column
              prop="ElecStationName1"
              label="电站名称"
              min-width="150"
            >
            </el-table-column>
            <el-table-column prop="ElecEqmName1" label="设备名称">
            </el-table-column>
            <el-table-column prop="Elec1" label="电能"> </el-table-column>
            <el-table-column prop="ElecA" label="尖峰"> </el-table-column>
            <el-table-column prop="ElecB" label="高峰"> </el-table-column>
            <el-table-column prop="ElecC" label="平"> </el-table-column>
            <el-table-column prop="ElecD" label="低谷"> </el-table-column>
          </el-table>
        </div>
        <div class="pieChart" id="pie"></div>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <div
          style="flex-grow: 1; display: flex; justify-content: space-between"
        >
          <div style="width: 48%; height: 100%">
            <fdChart ref="fd"></fdChart>
          </div>
          <div style="width: 48%; height: 100%">
            <fdBarChart ref="fdBar"></fdBarChart>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fdChart from "./components/totalReportChart/fdChart";
import fdBarChart from "./components/totalReportChart/fdBarChart";
import {
  areaList,
  companyList,
  companyelecstation,
  elecEqmList,
  fdcharts,
  fdcharts3,
} from "@/network/totalReport";
export default {
  components: {
    fdChart,
    fdBarChart,
  },
  data() {
    return {
      timeTab: ["年", "月", "日"],
      tTIndex: 2,
      areaOptions: [],
      areaVal: "",
      companyOptions: [],
      companyVal: "",
      elecstationOptions: [],
      elecstationVal: "",
      elecEqmOptions: [],
      elecEqmVal: "",
      date1: "",
      date2: "",
      tableData: [],
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.areaListFun();
  },
  methods: {
    changeTimeTab(index) {
      this.tTIndex = index;
      this.fdchartsFun();
    },
    areaListFun() {
      areaList()
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.areaOptions = content;
          }
        })
        .catch((err) => {});
    },
    companyListFun() {
      this.companyVal = "";
      companyList(this.areaVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.companyOptions = content.companyList;
          }
        })
        .catch((err) => {});
    },
    companyelecstationFun() {
      this.elecstationVal = "";
      companyelecstation(this.companyVal)
        .then((res) => {
          const { elecstation, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecstationOptions = elecstation;
          }
        })
        .catch((err) => {});
    },
    elecEqmListFun() {
      this.elecEqmVal = "";
      elecEqmList(this.elecstationVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecEqmOptions = content;
          }
        })
        .catch((err) => {});
    },
    fdchartsFun() {
      if (this.elecEqmVal == "") {
        this.$message({
          message: "请选择设备！",
          type: "error",
        });
        return;
      }
      fdcharts3(
        this.areaVal,
        this.companyVal,
        this.timeTab[this.tTIndex],
        this.date1,
        this.elecEqmVal,
        this.elecstationVal,
        this.date2
      )
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            console.log(content);
            this.tableData = content.line;
            this.setPieChart(this.tableData[0]);

            // this.$refs.fd.fillChart1(
            //   content.data2_1,
            //   content.data2_2,
            //   content.data2_3,
            //   content.data2_4
            // );
            this.$refs.fd.fillChart1(content.line);
            this.$refs.fdBar.fillChart1(content.line);
          }
        })
        .catch((err) => {});
    },
    rowClick(row) {
      this.setPieChart(row);
    },
    //设置饼图
    setPieChart(data) {
      let chart_projectLoad = this.$echarts.init(
        document.getElementById("pie")
      );
      chart_projectLoad.clear();
      var option = {
        tooltip: {
          trigger: "item",
        },
        legend: {
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "50%",
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: false,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: data.ElecA, name: "尖峰" },
              { value: data.ElecB, name: "高峰" },
              { value: data.ElecC, name: "平" },
              { value: data.ElecD, name: "低谷" },
            ],
          },
        ],
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
}
.right {
  width: 100%;
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
}
.search {
  display: flex;
  flex-wrap: wrap;
  padding-top: 0.6rem;
  position: relative;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    margin-bottom: 0.2rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  margin: 0rem 0 0;
  width: 80%;
}
.pieChart {
  width: 20%;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-range-editor .el-range-input {
  background: transparent;
  color: #fff;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/deep/ .el-date-editor .el-range-separator {
  color: #fff;
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}

.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
  height: 35px;
  margin-left: 0.2rem;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.02rem;
    .normalBk();
  }
}
.isActive {
  background: #0056dc !important;
}
</style>